










































































import { Vue, Component, Prop } from 'vue-property-decorator';

// vue-form-wizard
import VueFormWizard from 'vue-form-wizard';
import 'vue-form-wizard/dist/vue-form-wizard.min.css';
Vue.use(VueFormWizard);

import { EStepShape } from '@/../enums';

@Component({})
export default class Step extends Vue {
    //////////////////// prop ////////////////////

    @Prop({
        type: String,
        default: '#537895',
    })
    private barColor: string;

    @Prop({
        type: String,
        default: EStepShape.circle,
    })
    private shape: EStepShape.circle;

    @Prop({
        type: String,
        default: 'step',
    })
    private templateKey: string;

    @Prop({
        type: Array,
        default: () => {
            return [];
        },
    })
    private labels: [];

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    //////////////////// Private Function ////////////////////

    private onComplete() {
        this.$emit('complete');
    }

    //////////////////// Public Function ////////////////////

    public prevStep() {
        let formWizardRef: any = this.$refs.formWizard;
        if (formWizardRef) {
            formWizardRef.prevTab();
        }
    }

    public nextStep() {
        let formWizardRef: any = this.$refs.formWizard;
        if (formWizardRef) {
            formWizardRef.nextTab();
            this.$emit('on-complete');
        }
    }

    public changeStep(oldIndex: number, newIndex: number) {
        let formWizardRef: any = this.$refs.formWizard;
        if (formWizardRef) {
            formWizardRef.changeTab(oldIndex, newIndex); // changeTab(oldIndex, newIndex)
        }
    }

    public isLastStep() {
        let formWizardRef: any = this.$refs.formWizard;
        if (formWizardRef) {
            return (this.$refs as any).formWizard.isLastStep;
        }
        return false;
    }
}
Vue.component('step', Step);
